body {
  margin: 0;
  padding: 0;
  font-family: sans-serif
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: #fff
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
  animation: App-logo-spin infinite 20s linear;
  height: 80px
}

.App-title {
  font-size: 2em
}

@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg)
  }

  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn)
  }
}

@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg)
  }

  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn)
  }
}

.App-header-text {
  padding-left: 25px;
  padding-right: 25px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between
}

.Home {
  padding-left: 50px;
  padding-right: 50px;
  Display: flex;
  -ms-flex-direction: column;
  flex-direction: column
}

.App-proj-title {
  padding-top: 20px;
  padding-bottom: 10px
}

.proj-container {
  padding-left: 75px;
  padding-right: 75px;
  padding-bottom: 50px
}

.images {
  height: 150px;
  width: 300px;
  -ms-flex-item-align: center;
  align-self: center
}

.single_game {
  display: grid;
  grid-template-columns: 30% 70%
}

.body {
  Display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between
}

.button {
  height: 100%
}

#para {
  width: 70%
}



